<template>
  <div class="wrap">
    <div class="home"
      :style="{ width: `${roomWidth * ratio}px`, height: `${roomLength * ratio}px`}">
      <div
        v-for="item in objs"
        :key="item.name"
        :style="{ width: `${item.width * ratio}px`, height: `${item.length * ratio}px`,
                  top: `${item.top * ratio}px`, left: `${item.left * ratio}px`}"
        class="item"
        :class="{'dragging': item.dragging, 'reverting': item.revert}"
        @mousedown="handleMousedown(item)"
        @mouseup="handleMouseup"
        @mousemove="handleMousemove($event)"
        @mouseleave="handleMouseleave">
        {{item.name}}
        <img :src="item.img" class="intro-img" draggable="false" :style="{ width:
          `${item.revert ? item.length  * ratio: item.width * ratio}px`}">
      </div>
    </div>
    <div class="form-wrap">
      <label for="">房间: </label>
      <label for="">宽度</label>
      <input type="text" v-model="roomWidth">
      <label for="">长度</label>
      <input type="text" v-model="roomLength">
    </div>
    <div
      v-for="item in objs"
      :key="item.name"
      class="form-wrap"
    >
      <label for="">{{item.name}}: </label>
      <label for="">宽度</label>
      <input type="text" v-model="item.width">
      <label for="">长度</label>
      <input type="text" v-model="item.length">
      <button @click="revert(item)">翻转</button>
    </div>
  </div>
</template>
<script>
import sofa from '../assets/sofa.jpg';
import bed from '../assets/bed.jpg';
import run from '../assets/run.jpg';

export default {
  name: 'home',
  data() {
    return {
      objs: [
        {
          name: '床',
          length: 2.1, // 单位m
          width: 1.55,
          left: 1,
          top: 0,
          dragging: false,
          revert: false,
          img: bed,
          dragged: true,
        },
        {
          name: '沙发',
          length: 0.9, // 单位m
          width: 0.76,
          left: 3.2,
          top: 0,
          dragging: false,
          revert: false,
          img: sofa,
          dragged: true,
        },
        {
          name: '衣柜',
          length: 1.2, // 单位m
          width: 0.6,
          left: 0,
          top: 0,
          dragging: false,
          revert: false,
          dragged: true,
        },
        {
          name: '电脑桌',
          length: 1.2, // 单位m
          width: 0.6,
          left: 4,
          top: 2.1,
          dragging: false,
          revert: false,
          dragged: true,
        },
        {
          name: '书桌',
          length: 0.9, // 单位m
          width: 0.55,
          left: 0,
          top: 2,
          dragging: false,
          revert: false,
          dragged: true,
        },
        {
          name: '跑步机',
          length: 1.74, // 单位m
          width: 0.73,
          left: 3.8,
          top: 0,
          dragging: false,
          revert: false,
          img: run,
          dragged: true,
        },
        {
          name: '饮水机',
          length: 0.35, // 单位m
          width: 0.35,
          left: 3.2,
          top: 2.7,
          dragging: false,
          revert: false,
          dragged: true,
        },
        {
          name: '阳台',
          length: 1.87, // 单位m
          width: 0.2,
          left: 4.6,
          top: 0.81,
          dragging: false,
          revert: false,
          dragged: false,
        },
        {
          name: '大门',
          length: 0.2, // 单位m
          width: 0.95,
          left: 0,
          top: 3.3,
          dragging: false,
          revert: false,
          dragged: false,
        },
        {
          name: '厕所门',
          length: 0.85, // 单位m
          width: 0.2,
          left: -0.21,
          top: 2.45,
          dragging: false,
          revert: false,
          dragged: false,
        },
      ],
      dragging: false,
      curItem: null,
      ratio: 200,
      roomWidth: 4.6,
      roomLength: 3.3,
    };
  },
  created() {
    let layout = localStorage.getItem('layout');
    if (layout) {
      layout = JSON.parse(layout);
      this.objs = layout;
    }
  },
  methods: {
    change() {
      this.curTest = this.test;
      this.curTest.a = 2;
    },
    handleMousedown(item) {
      if (item.dragged) {
        this.dragging = true;
        this.curItem = item;
        this.curItem.dragging = true;
      }
    },
    handleMousemove(event) {
      if (this.dragging) {
        this.curItem.left = Math.max(
          Math.min(this.curItem.left + event.movementX / this.ratio,
            this.roomWidth - this.curItem.width),
          0,
        );
        this.curItem.top = Math.max(
          Math.min(this.curItem.top + event.movementY / this.ratio,
            this.roomLength - this.curItem.length),
          0,
        );
      }
    },
    handleMouseup() {
      this.cleanAndSave();
    },
    handleMouseleave() {
      this.cleanAndSave();
    },
    cleanAndSave() {
      if (this.curItem) {
        this.dragging = false;
        this.curItem.dragging = false;
        this.curItem = null;
        localStorage.setItem('layout', JSON.stringify(this.objs));
      }
    },
    revert(item) {
      const tmp = item.width;
      item.width = item.length; // eslint-disable-line
      item.length = tmp; // eslint-disable-line
      item.revert = !item.revert; // eslint-disable-line
    },
  },
};
</script>
